#whatsapp {
  bottom: 20px;
  right:20px;
  z-index: 1000;
  margin-bottom: auto
}

.whatsapp__fixed {
  position: fixed !important;

}
.whatsapp__fixed a::before {
  content: 'Te asesoramos por whatsapp';
}


#whatsapp a {
  //padding: 20px 0;
  text-decoration: none;
}

#whatsapp img {
  margin-bottom: 0;
}

#whatsapp a::before {

  display: inline-block;
  vertical-align: middle;
  padding: 5px 10px;
  margin-right: 10px;
  color: rgba(0, 0, 0, .8);
  background: white;
  border: 1px solid #ccc;
  border-radius: 20px;
  filter: drop-shadow(0 1px 4px rgba(0,0,0,.4));
  transition: .3s ease;
  opacity: 0;
}
#whatsapp:hover a::before {opacity: 1;}

